.container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}
.imagecontainer{
    width: 400px;
    height: 600px;
    overflow: hidden;
    position: relative;
    border-radius: 30px;
}
.image{
  border-radius: 30px;  
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.image:hover{
    
    -ms-transform: scale(1.3); /* IE 9 */
    -webkit-transform: scale(1.3); /* Safari 3-8 */
    transform: scale(1.3);
    transition: all 0.5s; 

}